.payments {
  background-color: #f8f8f6;
  min-height: 330px;
  padding: 50px 0;
}

.payments__inner {
  display: flex;
  background: #fff;
  padding: 30px 40px;
  align-items: center;

  h2 {
    font-size: 20px;
    font-family: inherit;
    font-weight: 500;
    line-height: 1.1;
    color: inherit;
    margin: 0 0 14px 0;
  }

  p {
    font-size: 15px;
    line-height: 22px;
    margin-bottom: 0;
    color: #333;
  }

  @media (max-width: $tablets-max) {
    flex-direction: column;
  }

  @media (max-width: $mobiles-max) {
    padding: 30px 15px 15px 15px;
  }
}

.payments__titles {
  width: 40%;
  margin-right: 8.5%;

  @media (max-width: $tablets-max) {
    width: 100%;
    margin-right: 0;
  }
}

.payments__image {
  height: 132px;

  @media (max-width: $tablets-max) {
    width: 100%;
  }

  @media (max-width: $mobiles-max) {
    height: 100px;
    margin-top: 15px;
  }

  img {
    object-fit: contain;
    width: 100%;
    height: 100%;
  }
}
