.popular-categories {
  overflow: hidden;
  margin-top: 25px;
}

.popular-categories__inner {
  h2 {
    margin: 0 30px 0 0;

    @media (max-width: $mobiles-max) {
      margin: 0 0 50px 0;
    }
  }
}

.popular-categories__swiper {
  margin-top: 48px;
  overflow: visible !important;

  @media (max-width: $mobiles-max) {
    margin-top: 14px;
  }
}

.popular-categories__slide {
}

.popular-categories__item {
  width: 33.33333333333%;
  transition: $transition-settings;
  display: flex;
  padding: 25px 35px 25px 35px;

  border-right: 1px solid #ececec;
  border-top: 1px solid #ececec;

  @media (min-width: $desktops) {
    &:first-of-type {
      padding: 25px 35px 25px 0;
    }

    &:nth-of-type(3n + 1) {
      padding: 25px 35px 25px 0;
    }

    &:first-of-type {
      padding: 25px 35px 25px 0;
      border-top: none;
    }

    &:nth-of-type(2) {
      border-top: none;
    }

    &:nth-of-type(4n) {
      padding: 25px 35px 25px 0;
    }

    &:nth-of-type(3n) {
      border-right: none;
    }

    &:nth-of-type(3) {
      border-top: none;
    }
  }

  @media (max-width: $tablets-max) {
    padding: 25px 15px;
    width: 50%;

    &:first-of-type {
      border-top: none;
    }

    &:nth-of-type(2) {
      border-top: none;
    }

    &:nth-of-type(even) {
      border-right: none;
    }
  }

  @media (max-width: $mobiles-max) {
    padding: 25px 0px;
    width: 100%;

    border-right: none;

    &:nth-of-type(2) {
      border-top: 1px solid #ececec;
    }
  }

  &:hover {
    .popular-categories__item-title {
      color: $color-red;
      cursor: pointer;
    }
  }
}

.popular-categories__item-avatar {
  width: 91px;
  height: 91px;
  flex-shrink: 0;

  img {
    object-fit: cover;
    width: 100%;
    height: 100%;
    border-radius: 50%;
  }
}

.popular-categories__item-title {
  line-height: 22px;
  font-size: 20px;
  font-weight: 600;
  max-height: 65px;
  overflow: hidden;
  color: #000;
  margin: 0;
  transition: $transition-settings;
}

.popular-categories__item-info {
  margin-left: 20px;
  display: flex;
  flex-direction: column;
}

.popular-categories__price-wrapper {
  letter-spacing: 0.5px;
  padding: 5px 6px;
  border-radius: 4px;
  background: $color-red;
  width: fit-content;
  color: #fff;
  line-height: 12px;
  display: flex;
  font-size: 12px;
  margin-top: auto;

  p {
    font-weight: 700;
    margin: 0;
  }
}

.popular-categories__items {
  display: flex;
  flex-wrap: wrap;
}

.popular-categories__month {
  text-align: center;
  font-weight: 500;
  line-height: 18px;
  font-size: 20px;
  margin: 0;
  padding: 8px 0;
}

.popular-categories__titles {
  display: flex;
  align-items: center;

  @media (max-width: $mobiles-max) {
    flex-wrap: wrap;
  }
}

.popular-categories__swiper-wrapper {
  overflow: hidden;
}

.popular-categories__more {
  position: relative;
  margin-left: auto;
  padding-right: 20px;
  a {
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 16px;
    display: flex;
    align-items: center;
    text-align: right;

    color: #000000;
  }

  &:after {
    position: absolute;
    content: "";
    background-image: url("/dist/img/arrow-red.svg");
    height: 18px;
    width: 9px;
    right: 0;
    top: 0;
    background-repeat: no-repeat;
  }
}
