.tour-page {
  padding: 20px 0;
  background: $background-alt-color;
}

.tour-page__wrapper {
  box-shadow: 0 0 6px 1px $color-btn-gray;
  background: $background-base-color;
}

.tour-page__inner {
  display: flex;
}

.tour-page__main {
  max-width: 854px;
}

.tour-page__image {
  height: 340px;
  overflow: hidden;

  &--thumb {
    height: 100px;
  }

  img {
    height: 100%;
    width: 100%;
    object-fit: cover;
  }
}

.tour-page__swiper {
  &-button {
    position: absolute;
    bottom: 0;
    z-index: 1;
    width: 30px;
    height: 100px;
    background: $color-light-black;
    opacity: 65%;
    backdrop-filter: blur(15px);
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;

    &--right {
      right: 0;
    }

    svg {
      stroke: $color-default-white;
      transition: $transition-settings;
    }

    &:hover {
      svg {
        stroke: $font-color-red;
      }
    }

    &--left {
      transform: rotate(180deg);
    }
  }
}

.tour-page__tabs {
  position: absolute;
  top: 0;
  width: 100%;
  max-width: calc($container - 30px);
  display: flex;
  justify-content: space-between;

  &-wrapper {
    position: relative;
    margin-bottom: 80px;
  }

  &--fixed {
    position: fixed;
  }

  &-item {
    transition: none;
    transition: color 0.3s ease, border 0.3s ease;
    flex-grow: 1;
    width: 100%;
    padding: 20px;
    text-align: center;
    background: $color-light-gray;
    border-bottom: 3px solid transparent;
    position: relative;
    top: -5px;
    z-index: 0;
    border-radius: 4px 4px 0 0;
    &:hover {
      color: $font-color-red;
      border-bottom: 3px solid $font-color-red;
      z-index: 999;
    }
  }
}

.tour-page__short {
  padding: 0 30px;
  margin-bottom: 20px;

  p {
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  ul {
    list-style: disc;
    padding-left: 15px;
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }

    li {
      &::marker {
        color: $font-color-red;
      }
    }
  }
}

.tour-page__desc {
  margin-top: 50px;
  padding: 20px 30px 0;
  border-left: 3px solid $color-bright-red;

  &-block {
    margin-bottom: 40px;

    &:last-child {
      margin-bottom: 50px;
    }
  }

  &-title {
    display: flex;
    flex-direction: column;
    font-size: 24px;
    line-height: 1.4;
    margin: 0 0 20px;
  }

  &-content {
    p {
      margin: 20px 0;

      &:last-child {
        margin-bottom: 0;
      }
    }

    ul {
      list-style: disc;
      padding-left: 15px;
      margin-bottom: 20px;

      &:last-child {
        margin-bottom: 0;
      }

      li {
        &::marker {
          color: $font-color-red;
        }
      }
    }

    img {
      margin: 15px 0;
      width: 100%;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}

.tour-page__review-link {
  display: block;
  padding: 30px;
  color: $font-color-red;
  font-size: 24px;

  &:hover {
    text-decoration: underline;
  }
}

.tour-page__accommodation {
  padding: 30px;

  h5 {
    font-size: 24px;
    margin-top: 0;
  }

  &-title-desc {
    font-size: 14px;
    font-family: 'Roboto';
  }

  &-sales {
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  ul {
    list-style: disc;
    padding-left: 15px;
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }

    li {
      &::marker {
        color: $font-color-red;
      }
    }
  }
}

.tour-page__sidebar {
  width: 100%;
  max-width: 406px;
  min-height: 1px;
  flex-shrink: 0;
  min-width: 290px;

  @media (max-width: $mobiles-max) {
    max-width: 100%;
    margin-right: 0;
  }
}

.tour-page__summary {
  padding: 30px;
  background: $color-light-gray;
}

.tour-page__price {
  padding-bottom: 25px;
  border-bottom: 1px solid $color-btn-gray;
  text-align: center;

  &-from {
    font-size: 16px;
  }

  &-price {
    font-family: 'tt_slabsbold';
    font-size: 36px;
    line-height: 1.1;
    color: #f10;
    margin-right: 5px;
  }

  &-currency {
    font-size: 36px;
  }

  &-person {
    display: block;
    transform: translate(0, -5px);
    font-size: 12px;
    color: $font-color-gray;
  }
}

.tour-page__days {
  padding: 25px 0;
  border-bottom: 1px solid $color-btn-gray;
  text-align: center;

  &::before {
    content: url(../img/time.svg);
    margin-right: 10px;
    vertical-align: text-top;
  }
}

.tour-page__next {
  &-title {
    padding: 25px 0;
    text-align: center;
    width: 100%;
    display: inline-block;

    &::before {
      content: url(../img/icon_calendar.svg);
      margin-right: 10px;
      vertical-align: text-top;
    }
  }

  &-items {
    padding: 0 15px 15px;
    background: $background-base-color;
  }

  &-item {
    display: flex;
    align-items: center;
    border-bottom: 1px solid $color-btn-gray;

    span {
      padding: 15px 0;
    }

    svg {
      margin-right: 10px;
    }
  }

  &-link {
    display: block;
    text-align: center;
    margin: 15px auto 0;

    span {
      border-bottom: 1px dashed;
    }

    &:hover {
      color: $font-color-red;
    }
  }
}

.tour-page__lists {
  display: flex;

  .tour-page__list {
    padding: 30px;
    flex-grow: 1;
    width: 100%;

    &:last-child {
      padding-top: 30px;
    }
  }
}

.tour-page__list {
  padding: 30px;

  &:last-child {
    padding-top: 0;
  }

  &-title {
    margin-top: 0;
  }

  &-subtitle {
    margin-top: 20px;
  }

  &-li {
    padding: 2px 0;

    &--included {
      &::before {
        content: url(../img/included.svg);
        margin-right: 10px;
        vertical-align: text-top;
      }
    }

    &--excluded {
      &::before {
        content: url(../img/excluded.svg);
        margin-right: 10px;
        vertical-align: text-top;
      }
    }
  }
}
