.call-order {
  min-height: 191px;
  background-color: #606060;

  @media (max-width: $mobiles-max) {
    padding: 30px 0 20px;
  }
}

.call-order__title {
  font-family: "Open Sans", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 36px;
  line-height: 49px;

  margin: 25px 0;

  color: #ffffff;

  @media (max-width: $mobiles-max) {
    margin: 0 0 25px 0;
  }
}

.call-order__fields {
  display: flex;
  column-gap: 10px;

  @media (max-width: $mobiles-max) {
    flex-direction: column;
    row-gap: 10px;
  }
}

.call-order__field {
  position: relative;
  display: flex;

  width: 38.5%;
  margin-right: 4%;

  @media (max-width: $mobiles-max) {
    width: 100%;
    margin-right: 0;
    margin-bottom: 20px;
  }
}

.call-order__inputs {
  display: flex;
  width: 65.5%;

  @media (max-width: $tablets-max) {
    width: 100%;
  }

  @media (max-width: $mobiles-max) {
    width: 100%;
    flex-direction: column;
  }
}

.call-order__input {
  width: 100%;
  height: 40px;
  padding: 0 0 0 18px;
  border: 1px solid #e4e4e4;
  transition: all $transition-settings;
  background-color: #606060;

  font-family: "Open Sans", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 19px;

  color: #ffffff !important;

  @media (max-width: $mobiles-max) {
    width: 100%;
  }

  // & &:focus {
  //   padding-top: 15px;
  // }

  // &:valid,
  // &:invalid {
  //   padding-top: 4px;
  // }
}

.call-order__message {
  display: flex;
  width: 100%;
  flex-direction: column;
}

.call-order__label {
  position: absolute;
  color: #a6a6a6;
  transition: opacity 0.3s ease;
  font-size: 0;
  position: absolute;
  top: 22px;
  left: 57px;
}

input:focus + label,
textarea:focus + label {
  font-size: 16px;
}

.call-order__field input:focus + .call-order__label,
.call-order__field input:not(:placeholder-shown) + .call-order__label {
  top: 14px;
  left: 22px;

  font-style: normal;
  font-weight: normal;
  font-size: 0;
  line-height: 19px;

  color: #ffffff;
}

.call-order__errors {
  width: 100%;
  position: absolute;
  left: 0;
}

.call-order__error {
  position: absolute;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
  letter-spacing: 0em;
  text-align: left;
  color: $color-red !important;
  right: 20px;
  top: 20px;
}

.call-order__submit {
  max-width: 190px;
}
