.requisites {
  h2 {
    margin: 50px 0;

    // @media (max-width: $mobiles-max) {
    //   margin: 0 0 50px 0;
    // }
  }
}

.requisites__items {
  display: flex;
  flex-direction: column;
  height: 245px;
  flex-wrap: wrap;
  margin-top: 50px;
  align-items: flex-start;
  width: 100%;

  @media (max-width: $tablets-max) {
    height: auto;
  }
}

.requisites__item {
  font-family: "Open Sans", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 15px;
  line-height: 24px;
  display: flex;
  justify-content: space-between;

  margin-bottom: 10px;

  color: #484848;

  width: 41%;
  margin-right: 5%;

  &:nth-of-type(even) {
    margin-right: 0;
  }

  @media (max-width: $tablets-max) {
    width: 100%;
    margin-right: 0;
  }
}

.requisites__title {
  display: flex;
  flex-grow: 1;
  flex-shrink: 0;

  p {
    white-space: nowrap;
  }

  hr {
    border: 0;
    border-bottom: 1px dotted #484848;
    background: #ffffff;
    width: 100%;
    opacity: 1;
    margin: 16px 0 0 0;
  }
}

.requisites__title + p {
  @media (max-width: 500px) {
    max-width: 230px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}
