.form {
  background: #ffffff;
  min-width: 820px;
  margin-top: 42px;
  width: 77%;
  min-height: 52px;
  height: 52px;
  border-radius: 3px;

  form {
    height: 100%;
  }

  @media (max-width: $tablets-max) {
    width: 100%;
    min-width: auto;
  }

  @media (max-width: $mobiles-max) {
    margin-top: 15px;
    padding: 0;
    height: auto;
  }

  &:focus {
    outline: none !important;
  }
}

.form__fields {
  display: flex;
  column-gap: 10px;

  @media (max-width: $mobiles-max) {
    flex-direction: column;
    row-gap: 10px;
  }
}

.form__inputs-wrapper {
  border: 1px solid #828282;
  border-right: none;
  display: flex;
  width: 75%;
  border-radius: 5px 0px 0px 5px;

  @media (max-width: $tablets-max) {
    width: 80%;
  }

  @media (max-width: $mobiles-max) {
    flex-direction: column;
    width: 100%;
    border-radius: 5px;
    border-right: 1px solid #828282;
  }
}

.form__field {
  position: relative;
  display: flex;
  flex-grow: 1;
  width: 33.33333%;
  border-right: 1px solid #828282;

  &:last-of-type {
    border-right: none;
  }

  @media (max-width: $mobiles-max) {
    width: 100%;
    height: 49px;
    border-right: none;

    border-bottom: 1px solid #828282;

    &:last-of-type {
      border-bottom: none;
    }
  }

  .vs__dropdown-toggle {
    height: 100%;
    border: none;
    border-radius: 0;
  }

  .vs__clear,
  .vs__open-indicator {
    display: none;
  }

  .v-select {
    width: 100%;
  }

  .vs__selected-options {
    padding: 0 0 0 35px;

    @media (max-width: $tablets-max) {
      padding: 0 0 0 25px;
    }
  }

  .vs__selected {
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 18px;

    color: #131313;
  }

  .vs__actions {
    padding: 0 16px 0 0;
    z-index: 2;

    @media (max-width: $tablets-max) {
      padding: 0 5px 0 0;
    }

    &::after {
      content: url("/dist/img/select-arrow.svg");
      width: 18px;
      height: 18px;
      transition: all $transition-settings;
    }
  }

  .vs--open .vs__actions {
    padding: 10px 16px 0 0;

    &::after {
      transform: rotate(180deg);
    }
  }

  [placeholder] {
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 18px;

    color: rgba(19, 19, 19, 0.7) !important;
  }
}

.form__inputs {
  display: flex;
  flex-direction: row;
  height: 100%;
  row-gap: 10px;

  @media (max-width: $mobiles-max) {
    flex-direction: column;
    row-gap: 14px;
  }
}

.form__input {
  width: 100%;
  background-color: $color-form-gray;
  height: 70px;
  padding: 0 15px 0 57px;
  border: 1px solid transparent;
  transition: all $transition-settings;
  box-shadow: inset 0px -1px 0px #d5d5d5;

  &--callback {
    height: 45px;
    padding: 15px 20px;
    background-color: $color-white;
    border: 1px solid $color-input-gray;
    margin-bottom: 10px;
  }

  &--textarea {
    height: 150px;
  }

  @media (max-width: $mobiles-max) {
    width: 100%;
  }

  & &:focus {
    padding-top: 15px;
  }

  &:valid,
  &:invalid {
    padding-top: 15px;
  }
}

.form__message {
  display: flex;
  width: 100%;
  flex-direction: column;
}

.form__label {
  position: absolute;
  top: 14px;
  left: 58px;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;

  &::before {
    position: absolute;
    content: "";
    width: 14px;
    height: 14px;
    background-repeat: no-repeat;

    top: 4px;
    left: -35px;
    transition: none;

    @media (max-width: $tablets-max) {
      left: -45px;
    }
  }

  &--calendar {
    &::before {
      background-image: url("/dist/img/icon_calendar.svg");
    }
  }

  &--days {
    &::before {
      background-image: url("/dist/img/time.svg");
    }
  }

  &--country {
    &::before {
      background-image: url("/dist/img/location.svg");
    }
  }
}

.form__field input:focus + .form__label,
.form__field input:not(:placeholder-shown) + .form__label {
  top: 12px;
  left: 57px;
  font-size: 12px;

  &--name {
    &::before {
      background-image: url("/dist/img/user-active.svg");
      top: 15px;
      left: -35px;
    }
  }

  &--phone {
    &::before {
      background-image: url("/dist/img/phone-active.svg");
      top: 15px;
      left: -35px;
    }
  }
}

.form__errors {
  width: 100%;
  position: absolute;
  left: 0;
}

.form__error {
  position: absolute;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
  letter-spacing: 0em;
  text-align: left;
  color: $color-red !important;
  right: 20px;
  top: 27px;
}

.form__submit {
  max-width: 190px;
  transition: $transition-settings;
  &--callback {
    max-width: 100%;
    width: 100%;
    line-height: 20px;
    height: 45px;
    background: $font-color-red;
    border-radius: 5px;
    color: $color-white;
    font-weight: 600;
    font-size: 18px;
    border: 1px solid transparent;
    &:focus {
      color: $color-alt-black;
      background-color: $color-btn-gray;
      border-color: $color-border-light-gray;
    }
  }
}

.form__rating {
  margin-bottom: 10px;
}

.form__field-files {
  position: relative;
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  input {
    position: absolute;
    opacity: 0 !important;
    padding: 0;
    width: 30px;
    height: 30px;
    left: 0;
  }
  img {
    cursor: pointer;
    &.preview {
      margin: 0 0 0 10px;
    }
  }
  .close {
    stroke: $color-black;
    opacity: 0.5;
    transition: all $transition-settings;
    &:hover {
      opacity: 1;
    }
  }
}