.prices-table {
  padding: 40px 0;
  overflow: hidden;
  h4 {
    font-size: 20px;
    font-family: inherit;
    font-weight: 500;
    line-height: 1.1;
    color: inherit;
    margin: 0;
  }
}

.prices-table__items {
  display: flex;
  flex-direction: column;
  height: 185px;
  flex-wrap: wrap;
  padding: 38px 0;

  @media (max-width: $tablets-max) {
    height: 274px;
  }

  @media (max-width: 600px) {
    height: auto;
  }
}

.prices-table__item {
  display: flex;
  justify-content: space-between;
  width: 23.5%;

  margin-right: 2%;
  margin-bottom: 12px;
  padding-right: 2.5%;

  p {
    margin: 0;
  }

  @media (max-width: $tablets-max) {
    width: 49%;

    margin-right: 2%;
  }

  @media (max-width: 600px) {
    width: 100%;

    margin-right: 0;
  }
}

.prices-table__name {
  position: relative;
  font-weight: 500;
  text-decoration: underline;
  letter-spacing: 0.5px;
  padding-right: 8px;
  font-size: 14px;
  background: #fff;
  color: #000;
  transition: $transition-settings;
  z-index: 9;

  &:after {
    content: "";
    width: 3px;
    height: 3px;
    background-color: #ed1c21;
    display: block;
    position: absolute;
    right: 0;
    top: 11px;
  }
}

.prices-table__price {
  line-height: 12px;
  display: inline-block;
  font-size: 12px;
  letter-spacing: 0.5px;
  padding: 4px 6px;
  border-radius: 4px;
  background: #ec1c20;
  width: fit-content;
  color: #fff;
  position: relative;

  &:before {
    content: "";
    height: 3px;
    display: block;
    position: absolute;
    right: 108%;
    top: 10px;
    border-bottom: 1px dashed #cececc;
    width: 200%;

    @media (max-width: 600px) {
      width: 300%;
    }
  }
}

.prices-table__title {
  display: flex;
  align-items: center;

  svg {
    margin-right: 12px;
  }
}
