.months {
  width: 150px;
}

.months__swiper {
  position: relative;
}

.months__swiper-button {
  cursor: pointer;
  position: absolute;
  outline: none;
  top: 5px;
  border: none;
  width: 25px;
  height: 25px;
  background: #606060;
  border-radius: 50%;
  background-position: center;
  z-index: 7;

  &--categories-left {
    background-image: url("/dist/img/arrow.svg");
    background-repeat: no-repeat;
    left: 0;
  }

  &--categories-right {
    right: 0;
    background-image: url("/dist/img/arrow.svg");
    background-repeat: no-repeat;
    transform: rotate(180deg);
  }
  &--tours-left {
    background-image: url("/dist/img/arrow.svg");
    background-repeat: no-repeat;
    left: 0;
  }

  &--tours-right {
    right: 0;
    background-image: url("/dist/img/arrow.svg");
    background-repeat: no-repeat;
    transform: rotate(180deg);
  }
}

.months__name {
  text-align: center;
  font-weight: 500;
  line-height: 18px;
  font-size: 20px;
  margin: 0;
  padding: 8px 0;
  // top: -70px;
  // left: 31%;
}
