.header__phones + .select-modal {
  position: absolute;
  top: 15px;
  right: 150px;
  max-width: 740px;

  @media (max-width: $mobiles-max) {
    top: 30px;
    left: 70px;
    min-width: 220px;
  }
}

.footer__phones + .select-modal {
  position: absolute;
  top: 10px;
  right: 0;
  max-width: 740px;
}

.select-modal__cities {
  margin: 20px 0 0;
  background-color: #fff;
  box-shadow: 0 0 11px rgb(16 90 140 / 34%);
}

.select-modal__city {
  padding: 5px 20px;
  cursor: pointer;
  white-space: nowrap;
  font-size: 14px;

  &:hover {
    background-color: #f10;
  }
}

.select-modal {
  display: none;
  z-index: 1;

  &--opened {
    display: block;
  }
}
