.button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  outline: none !important;
  border: none;
  transition: $transition-settings;
  cursor: pointer;
  flex-shrink: 0;
  font-family: "Open Sans";
  background: none;

  &--callback {
    display: inline-block;
    margin-top: 3px;
    font-weight: 300;
    font-size: 15px;
    text-decoration: underline;
    width: fit-content;
    padding: 0;
  }

  &--useful {
    width: 145px;
    font-size: 14px;
    margin: 15px 5px 0 0;

    color: #fff;
    background: #008f1f no-repeat;
    padding: 9px 20px 9px 28px;

    img {
      margin-left: 10px;
    }
  }

  &--useless {
    width: 145px;
    font-size: 14px;
    margin: 15px 5px 0 0;

    color: #000;
    border: 1px #000 solid;
    font-weight: 500;
    padding: 8px 20px 8px 25px;
  }

  &--red {
    background: $color-red;
    border-radius: 0 5px 5px 0;

    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;

    color: #ffffff;

    padding: 22px 0;
    width: 25%;
    transition: $transition-settings;

    @media (max-width: $tablets-max) {
      width: 20%;
    }

    @media (max-width: $mobiles-max) {
      font-size: 18px;
      font-style: normal;
      font-weight: 500;
      line-height: 27px;
      letter-spacing: 0em;
      text-align: center;
      width: 100%;
      padding: 12px 0;
      border-radius: 5px;
    }

    &:hover {
      background: $color-hover-red;
    }

    &:active {
      background: $color-active-red;
    }
  }

  &--order-red {
    background: #ec1c20;
    border-radius: 3px;

    font-family: "Open Sans", sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 16px;
    display: flex;
    align-items: center;
    text-align: center;

    color: #ffffff;

    color: #ffffff;

    padding: 10px 0;
    width: 15%;
    min-width: 120px;
    transition: $transition-settings;

    @media (max-width: $tablets-max) {
      width: 20%;
    }

    @media (max-width: $mobiles-max) {
      color: #fff;
      background-color: #d9534f;
      border-color: #d43f3a;

      display: inline-block;
      padding: 6px 12px;
      margin-bottom: 0;
      font-size: 14px;
      font-weight: 400;
      line-height: 1.42857143;
      text-align: center;
      white-space: nowrap;
      background-image: none;
      border: 1px solid transparent;
      border-radius: 4px;
    }

    &:hover {
      background: $color-hover-red;
    }

    &:active {
      background: $color-active-red;
    }
  }

  &--controls {
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 24px;
    background-color: $color-light-gray;
    border: 1px solid $color-red;
    border-radius: 5px;

    padding: 10px 0;
    display: flex;
    align-items: center;

    svg {
      margin-left: 10px;
    }
  }

  &--show-details {
    color: #000;
    background-color: #fff;
    border-color: #fff;
    width: 160px;
    font-size: 14px;

    padding: 7px 20px;
    min-height: 40px;
    transition: $transition-settings;
    border: 1px solid transparent;
    border-radius: 4px;

    &:hover {
      background: #eee;
    }
  }

  &--swiper {
    outline: none;
    border: none;
    width: 160px !important;
    height: 160px !important;
    transition: $transition-settings;
    border-radius: 5px;

    svg {
      width: 25px;
      height: 50px;

      fill: $color-default-white;
    }

    @media (max-width: $mobiles-max) {
      width: 30px !important;
      height: 30px !important;
    }

    &:after {
      display: none;
    }

    &:hover {
      svg {
        fill: $color-red;
      }
    }
  }

  &--watched {
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 16px;

    color: #131313;

    display: flex;
    align-items: center;
    background: #ffffff;
    border-radius: 5px;
    padding: 12px 0;
    margin-top: 30px;

    svg {
      margin-right: 10px;
    }
  }

  &--search {
    position: absolute;
    width: 19px;
    height: 19px;
    right: 18px;
    bottom: 25px;

    padding: 0;
  }

  &--reviews-next {
    position: absolute;
    top: -78px !important;
    right: 0 !important;
    left: auto;
    border-radius: 0 5px 5px 0;
  }

  &--reviews-prev {
    transform: rotate(180deg);
    position: absolute;
    top: -78px !important;
    right: 50px !important;
    left: auto !important;
    border-radius: 0 5px 5px 0;
  }

  &:hover {
    p {
      color: $color-black;
    }
  }

  &--backtotop {
    right: 4vh;
    bottom: 4vh;
    position: fixed;
    z-index: 100;
    width: 40px !important;
    height: 40px !important;
    background-color: $color-red !important;
    border-radius: 4px;

    svg {
      transform: rotate(-90deg);
    }

    &:hover {
      background-color: $color-dark-red !important;
    }

    &:active {
      background-color: $color-dark-red !important;
    }

    @media (max-width: $mobiles-max) {
      right: 15px;
      bottom: 15px;
    }
  }

  &--showmore {
    max-width: 220px;
    font-size: 18px;
    font-weight: 500;
    padding: 20px 40px;
    background: transparent;
    border: 2px solid $font-color-red;
    border-radius: 10px;
    cursor: pointer;
    &:hover {
      color: $font-color-red;
    }
  }
}
