.callback {
  width: 100%;
  padding: 30px 40px;
  margin-bottom: 30px;
  border-radius: 15px;
  border: 1px solid $color-gray-line;

  @media (max-width: $tablets-max) {
    max-width: 100%;
  }

  &--category {
    border: none;

    box-shadow: 0px 4px 40px rgb(0 0 0 / 10%);

    @media (min-width: $desktops) {
      max-width: calc(50% - 30px);
    }

    .form__input {
      line-height: 16px;
      font-size: 20px;
      text-align: center;
    }
  }
}

.callback__title {
  display: block;
  margin-bottom: 18px;
  text-align: center;
}

.callback__text {
  text-align: center;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
}

.callback__form {
  display: flex;
  flex-direction: column;
  min-width: 100%;
  width: 100%;
  min-height: fit-content;
  height: fit-content;
  margin-top: 20px;

  @media (max-width: $mobiles-max) {
    padding: 0;
  }
}
