.review-sites {
  padding: 80px 0;
  background: $color-gray-line;
}

.review-sites__inner {
  display: flex;
  background: $color-white;
  box-shadow: 0px 4px 40px rgb(0 0 0 / 10%);
  border-radius: 15px;
  padding: 40px 40px 25px;
  @media (max-width: $tablets-max) {
    flex-direction: column;
  }
  @media (max-width: $mobiles-max) {
    padding: 15px 15px 25px;
  }
}

.review-sites__texts {
   width: 100%;
   flex-shrink: 0;
  max-width: 35%;
  margin-right: 2%;
  display: flex;
  flex-direction: column;
  @media (max-width: $tablets-max) {
    max-width: 100%;
  }

  &-title {
    font-size: 25px;
    line-height: 29px;
    margin-top: 0;
  }

  .reestr__logo {
     margin-right: 28px;
  }

  .reestr__texts p {
   font-size: 14px;
   line-height: 14px;
   color: $font-color-gray;
   margin-bottom: 8px;
  }
}

.review-sites__links {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;

  &-item {
     width: 137px;
    height: 85px;
    opacity: 0.5;
    border: 1px solid #e8e7e7;
    border-radius: 15px;
    margin-left: 40px;
    padding: 10px 7px;
    margin-bottom: 15px;
    display: flex;
    justify-content: center;
    transition: $transition-settings;
    &:hover {
       opacity: 1;
    }

    @media (max-width: $tablets-max) {
      margin: 0 auto 15px;
    }
  }
}
