.regions {
  overflow: hidden;
  margin-top: 25px;
}

.regions__inner {
  h2 {
    margin: 0 30px 0 0;

    // @media (max-width: $mobiles-max) {
    //   margin: 0 0 50px 0;
    // }
  }
}

.regions__item {
  a {
    display: flex;
  }
  width: 33.33333333333%;
  transition: $transition-settings;

  padding: 25px 35px 25px 35px;

  border-right: 1px solid #ececec;
  border-bottom: 1px solid #ececec;

  &:first-of-type {
    padding: 25px 35px 25px 0;
  }

  &:nth-of-type(3n + 1) {
    padding: 25px 35px 25px 0;
  }

  @media (max-width: $tablets-max) {
    padding: 25px 15px;
    width: 50%;

    &:nth-of-type(3n + 1) {
      padding: 25px 15px;
    }

    &:first-of-type {
      border-top: none;
    }

    &:nth-of-type(2) {
      border-top: none;
    }

    &:nth-of-type(even) {
      border-right: none;
    }
  }

  @media (max-width: $mobiles-max) {
    padding: 25px 0px;
    width: 100%;

    border-right: none;

    &:nth-of-type(3n + 1) {
      padding: 25px 0;
    }

    &:nth-of-type(2) {
      border-top: 1px solid #ececec;
    }
  }

  img {
    border-radius: 50%;
  }

  &:hover {
    .regions__item-title {
      color: $color-red;
      cursor: pointer;
    }
  }
}

.regions__item-avatar {
  width: 91px;
  height: 91px;
  flex-shrink: 0;

  img {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
}

.regions__item-title {
  line-height: 22px;
  font-size: 20px;
  font-weight: 600;
  max-height: 65px;
  overflow: hidden;
  color: #000;
  margin: 0;
  transition: $transition-settings;
}

.regions__item-info {
  margin-left: 20px;
  display: flex;
  flex-direction: column;
}

.regions__price-wrapper {
  letter-spacing: 0.5px;
  padding: 5px 6px;
  border-radius: 4px;
  background: $color-red;
  width: fit-content;
  color: #fff;
  line-height: 12px;
  display: flex;
  font-size: 12px;
  margin-top: auto;

  p {
    font-weight: 700;
    margin: 0;
  }
}

.regions__items {
  display: flex;
  flex-wrap: wrap;
}

.regions__month {
  text-align: center;
  font-weight: 500;
  line-height: 18px;
  font-size: 20px;
  margin: 0;
  padding: 8px 0;
  // top: -70px;
  // left: 31%;
}

.regions__titles {
  display: flex;
  align-items: center;
}
