.header {
  transition: $transition-settings;
  width: 100%;
  z-index: 50;
  transform: translateY(0);
  position: fixed;

  &--opened {
  }
}

.header__main-inner {
  display: flex;
  align-items: flex-start;
  padding-top: 17px;
  padding-bottom: 10px;

  @media (max-width: $tablets-max) {
    justify-content: space-between;
  }

  @media (max-width: $mobiles-max) {
    background-color: #fff;

    margin-left: -24px;
    margin-right: -24px;
    padding-left: 24px;
    padding-right: 24px;
  }
}

.header__logo {
  width: 176px;
  height: 46px;
  flex-shrink: 0;

  @media (max-width: $tablets-max) {
    width: 90px;
    height: 36px;
  }

  @media (max-width: 849px) {
    margin-right: 20px;
  }

  @media (max-width: $mobiles-max) {
    width: 125px;
    height: 38px;
  }

  img {
    object-fit: contain;
    width: 100%;
    height: 100%;
  }
}

.header__top {
  position: relative;
  z-index: 2;
  transform: translateY(-150%);
  animation: ani 1s forwards;
  min-height: 30px;
  background-color: #f7f6f6;
}

.header__top-inner {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  position: relative;
  padding: 6px 0;

  @media (max-width: $mobiles-max) {
    justify-content: space-between;
    align-items: center;
    min-height: 80px;
    margin-bottom: 12px;
  }
}

.header__top-links {
  display: flex;
  justify-content: space-between;
  width: 37.5%;
}

.header__navigation {
  display: flex;
  width: 45%;
  padding-top: 6px;
  margin: 0 auto;
  justify-content: space-between;
  transition: $transition-settings;
  min-width: 460px;

  @media (max-width: 1249px) {
    width: 58%;
  }

  @media (max-width: $mobiles-max) {
    margin-left: 0;
    margin-right: 0;
    width: 100%;
    flex-direction: column;
    border-bottom: 1px solid rgba(130, 130, 130, 0.2);
    border-top: 1px solid rgba(130, 130, 130, 0.2);
    padding: 30px 0;
  }
}

.header__navigation-item {
  &:first-of-type {
    .header__navigation-link {
      &::before {
        display: none;
      }
    }
  }

  @media (max-width: $mobiles-max) {
    padding-bottom: 31px;

    &:last-of-type {
      padding-bottom: 0;
    }
  }

  svg {
    margin-left: 4px;
  }
}

.header__icons-wrapper {
  display: flex;
  padding-right: 15px;
  padding-top: 6px;

  justify-content: flex-end;
  overflow: hidden;
  width: 150px;

  @media (max-width: $mobiles-max) {
    width: 100%;
    flex-direction: column;
    margin-top: 26px;
  }
}

.header__icons {
  display: flex;
  width: 95px;
  transition: $transition-settings;
  justify-content: flex-end;

  span {
    font-style: normal;
    font-weight: bold;
    font-size: 9px;
    line-height: 12px;
    text-align: center;
    letter-spacing: 0.02em;
    color: $color-default-white;
    background-color: $color-red;
    border-radius: 50%;
    position: absolute;
    bottom: 0;
    right: 6px;
    padding: 0 3px;
  }
}

.header__icon {
  position: relative;
  a {
    display: block;
  }

  svg {
    fill: #000000;
  }

  .button {
    transition: $transition-settings;
    &:hover {
      svg {
        fill: #ec1c20;
      }
    }
  }
}

.header__mobile-content {
  display: flex;
  align-items: center;

  @media (max-width: $tablets-max) {
    width: 140px;
    justify-content: space-between;
  }
}

.header__menu {
  position: fixed;
  top: 0;
  background-color: $color-default-black;
  top: 0px;
  width: 100%;
  padding-top: 61px;
  z-index: 2;
  padding-bottom: 80px;

  height: 100vh;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
}

.header__navigation-link {
  font-style: normal;
  font-weight: 600;
  font-size: 15px;
  line-height: 20px;
  text-transform: uppercase;
  font-weight: bold;
  position: relative;
  transition: $transition-settings;
  border-bottom: 3px solid $color-default-white;

  &:before {
    position: absolute;
    content: "";
    width: 10px;
    height: 10px;
    left: -15px;
    top: 4px;

    background-image: url("/dist/img/romb.svg");
  }

  &:hover,
  &:active {
    cursor: pointer;
    color: $color-red;
    margin: 0;
    transition: 0.3s all;
    border-bottom: 3px solid $color-red;
  }

  @media (max-width: $mobiles-max) {
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 21px;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: #131313;
    font-weight: 500;
    border-bottom: none;

    &:before {
      display: none;
    }
  }
}

.header__top-link {
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 19px;
  text-align: right;
  color: #2a2a2a;
  text-decoration: underline;

  &--phone {
    font-style: normal;
    font-weight: bold;
    font-size: 15px;
    line-height: 20px;
    text-align: right;
    letter-spacing: 0.02em;
    text-decoration: none;
  }
}

.header__main {
  animation: ani 1s forwards;
  transform: translateY(-150%);
  animation-delay: 0.2s;
  background-color: #fff;
}

@keyframes ani {
  0% {
    transform: translateY(-150%);
  }

  100% {
    transform: translateY(0);
  }
}

.header__choose-city {
  display: flex;
  align-items: center;

  @media (max-width: $mobiles-max) {
    width: 55%;
    max-width: 176px;
    flex-wrap: nowrap;
  }
  p,
  span {
    font-size: 13px;
    margin: 0;

    white-space: nowrap;
  }

  svg {
    margin-left: 8px;
  }
}

.header__city {
  color: red;

  &:hover {
    cursor: pointer;
  }
}

.header__phones {
  display: flex;
  width: 20%;
  margin-left: 3%;
  flex-wrap: nowrap;
  min-width: 245px;

  @media (max-width: $mobiles-max) {
    flex-direction: column;
    min-width: auto;
    width: 43%;
    margin-left: 2%;
    align-items: flex-end;
  }

  a {
    font-size: 13px;
    font-style: normal;
    font-weight: 700;
    line-height: 16px;
    letter-spacing: 0em;
    text-align: left;
    display: flex;
    flex-wrap: nowrap;

    @media (max-width: $mobiles-max) {
      font-style: normal;
      font-weight: normal;
      font-size: 13px;
      line-height: 16px;
      color: #131313;
      margin: 0;

      align-items: center;
      margin-bottom: 19px;

      svg {
        margin-right: 12px;
      }
    }

    &:first-of-type {
      margin-right: 16%;

      @media (max-width: $mobiles-max) {
        margin-right: 0;
      }
    }

    &:last-of-type {
      margin-bottom: 0;
    }
  }
}

.header__city-icon-rotated {
  transition: $transition-settings;
  transform: rotate(180deg);
}

.header__button {
  background: #f7f6f6;
  border: 1px solid rgba(130, 130, 130, 0.2);
  border-radius: 5px;
  width: 97px;
  min-height: 38px;
  display: flex;
  align-items: center;
  justify-content: center;

  span {
    margin-left: 12px;
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 16px;

    color: #4f4f4f;
  }
}
.header__mobile-wrapper {
  @media (max-width: $mobiles-max) {
    background: #f7f6f6;
    height: 100vh;
  }
}

.header__mobile {
  &--opened {
    overflow: auto;
  }
}

.header__search-input {
  transform: translateX(200px);
  transition: $transition-settings;
  position: absolute;

  &--visible {
    transform: translateX(0);

    top: -4px;
    display: block;
    position: absolute;
    right: 25px;
    height: 25px;
    width: 110px !important;
    line-height: 20px;
    padding: 0 5px;
    background: 0;
    border: 0;
    outline: 0;
    color: #777;
    font-size: 13px;
    font-weight: normal;
    background: #ffffff;
  }
}
