.footer {
  overflow: hidden;
}

.footer__bottom {
  background: #ff0000;
  min-height: 98px;
  padding: 15px 0;

  p {
    color: #fff;
    font-size: 17px;
    text-align: center;
  }
}

.footer__top {
  padding: 0 14px;
  display: flex;
  margin-bottom: 48px;

  @media (max-width: $tablets-max) {
    padding: 0;
  }
}

.footer__callback {
  display: flex;
  flex-direction: column;
  position: relative;

  width: 20%;
  margin-right: 1.5%;
}

.footer__sections {
  display: flex;
  width: 78.5%;
  align-items: baseline;

  @media (max-width: $tablets-max) {
    flex-direction: column;
    width: 100%;
  }
}

.footer__section {
  display: inline-block;
  vertical-align: top;
  border-left: 2px #e6e6e6 solid;
  padding: 0 25px 0 14px;
  // max-width: 300px;

  @media (max-width: $tablets-max) {
    border-left: none;
    width: 100%;
    padding: 0;
  }
}

.footer__section-link {
  margin-bottom: 2px;
  display: flex;

  span {
    color: #000;
    font-weight: 500;
    transition: $transition-settings;
    font-size: 15px;
  }
  a {
    color: #000;
    font-weight: 100;
    transition: $transition-settings;
    font-size: 15px;

    &:hover {
      color: red;
      text-decoration: none;
    }
  }

  @media (max-width: $tablets-max) {
    flex-direction: column;
    align-items: center;
  }
}

.footer__choose-city {
  display: flex;

  font-size: 15px;
}

.footer__city {
  color: red;

  &:hover {
    cursor: pointer;
  }
}

.footer__section-title {
  font-weight: 500;
  font-size: 15px;

  @media (max-width: $tablets-max) {
    background: #e6e6e6 no-repeat calc(100% - 20px) center;
    padding: 10px 30px 10px 20px;
    font-weight: 700;

    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.footer__socials {
  display: flex;
  justify-content: center;
}

.footer__social {
  margin-right: 10px;
}

.footer__phones {
  a {
    display: inline-block;
    width: 100%;
    color: #000000;
    font-size: 23px;
    font-weight: 600;
  }
}
