.article {
  margin-bottom: 105px;

  @media (max-width: $mobiles-max) {
    margin-bottom: 95px;
  }

  ul {
    list-style: disc !important;
    margin-bottom: 12px;
  }

  h2 {
    font-weight: 500;
    line-height: 1.1;
    color: inherit;
    font-size: 1.5em;
    font-family: "Museo Sans";
    // margin: 19px 0;
    margin: 15px 0;
  }

  p {
    margin-bottom: 20px;
    line-height: 20px;
  }

  a {
    color: red;
  }

  blockquote {
    border-left: 5px solid #ccc;
    margin: 27px 10px;
    padding: 0.5em 10px;

    p {
      margin: 0;
      font-size: 17.5px;
      line-height: 25px;
    }

    em {
      font-size: 14px;
    }
  }

  table {
    width: 59%;

    @media (max-width: $mobiles-max) {
      width: 100%;
    }

    tr {
      display: flex;

      @media (max-width: $mobiles-max) {
        flex-direction: column;
      }
      td {
        width: 50%;

        @media (max-width: $mobiles-max) {
          padding-right: 10px;
          width: 100%;
        }
      }
    }
  }

  .vnz-widget {
    max-width: none;

    @media (max-width: $mobiles-max) {
      margin: 0 auto;
      max-width: 320px;
    }
  }
}

.article__inner {
  margin: 40px auto 0;
  padding: 0 20px;

  @media (max-width: $mobiles-max) {
    padding: 0 10px;
  }

  &--about {
    font-family: "Museo Sans";
    font-weight: 300;

    h2 {
      font-family: "Museo Sans";
      font-weight: 300;
    }
  }

  &--holiday {
    font-family: "Museo Sans";
    font-weight: 300;

    h2 {
      font-family: "Museo Sans";
      font-weight: 300;
    }
  }
}

.article__share-title {
  font-size: 22px;
  margin-top: 30px;

  font-family: "Museo Sans", sans-serif;

  margin-left: 10%;
  margin-right: auto;
  font-weight: 400;

  @media (max-width: $tablets-max) {
    display: block;
    flex-grow: 1;
    width: 100%;
    text-align: center;
    margin-left: 0;
  }

  @media (max-width: $mobiles-max) {
    font-size: 19px;
    margin-top: 30px;

    margin-right: auto;
    font-weight: 300;
  }
}

.article__buttons {
  display: flex;

  @media (max-width: $tablets-max) {
    justify-content: center;
    flex-wrap: wrap;
  }
}

.article__frame {
  iframe {
    border: 0px;
    width: 100%;
    overflow: hidden;
  }

  @media (max-width: $mobiles-max) {
    margin-left: -25px;
    margin-right: -25px;
  }
}

.article__images {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  @media (max-width: 600px) {
    flex-direction: column;
    max-width: 300px;
    margin: 0 auto;
  }
}

.article__image {
  width: 32%;
  margin-right: 2%;

  img {
    object-fit: contain;
    width: 100%;
    height: 100%;
  }

  &:nth-of-type(3n) {
    margin-right: 0;
  }

  @media (max-width: $mobiles-max) {
    width: 49%;
    margin-right: 2%;

    &:nth-of-type(3n) {
      margin-right: 0;
    }

    &:nth-of-type(2n) {
      margin-right: 0;
    }
  }

  @media (max-width: 600px) {
    width: 100%;
    margin-right: 0;

    &:nth-of-type(3n) {
      margin-right: 0;
    }

    &:nth-of-type(2n) {
      margin-right: 0;
    }
  }
}
