.tour {
  width: 100%;
  max-width: calc(50% - 15px);
  box-shadow: 0px 4px 40px rgb(0 0 0 / 10%);
  border-radius: 15px;
  margin-bottom: 30px;
  @media (max-width: $tablets-max) {
    max-width: 100%;
  }
}

.tour__image {
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  border-radius: 15px 15px 0 0;
  height: 200px;
  // @media (max-width: $tablets-max) {
  //   height: auto;
  // }

  &::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 60%;
    background: linear-gradient(360deg, rgba(36, 36, 36, 0.7) 25%, rgba(36, 36, 36, 0) 100%);

  }

  img {
    transition: all 2s;

  }

  &:hover {
    img {
      transform: scale(1.2);
    }
  }

  &-sale {
    position: absolute;
    top: 0;
    z-index: 1;
    font-weight: 500;
    padding: 0 15px;
    color: $color-white;
    text-align: center;
    display: block;
    height: 28px;
    width: 100%;
    line-height: 28px;
    background: $font-color-red;
  }

  &-tags {
    position: absolute;
    bottom: 0;
    display: flex;
    flex-wrap: wrap;
    z-index: 1;
    margin: 15px 25px;
  }

  &-tag {
    cursor: pointer;
    color: $color-white;
    font-size: 11px;
    display: inline-block;
    margin-right: 3px;
    transition: $transition-settings;

    &:hover {
      color: $font-color-red;
    }
  }
}

.tour__texts {
  padding: 15px 25px 35px 25px;
  background: $color-white;
  border-radius: 0 0 15px 15px;
  &:hover {
    .tour__title {
      text-decoration: underline;
      color: $font-color-red;
    }
  }
  hr {
    margin-top: 20px;
    margin-bottom: 20px;
    border: 0;
    border-top: 1px solid $color-gray-line;
  }
}

.tour__category {
  display: inline-block;
  text-decoration: none;
  padding: 2px 5px;
  font-size: 11px;
  line-height: 1.4;
  background: $color-light-gray;
  border-radius: 3px;
  margin-right: 5px;
  margin-bottom: 20px;
}

.tour__title {
  display: block;
  font-weight: 600;
  font-size: 20px;
  line-height: 30px;
  transition: $transition-settings;
}

.tour__desc {
  margin-top: 16px;
  line-height: 22px;
}


.tour__text {
  margin: 8px 0;
  line-height: 22px;
  &--bold {
    font-weight: 700;
  }
}

.tour__price {
  font-size: 26px;
  margin: 0;
  font-weight: 600;
  margin-top: 16px;
}

.tour__rating {
  margin-top: 20px;
  display: flex;
  align-items: flex-end;
}

.tour__stars {
  display: flex;
  &-item {
    margin-right: 6px;
    i {
      &::after {
        content: '';
        display: block;
        background-image: url(../img/star.svg);
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center center;
        width: 20px;
        height: 20px;
      }
    }
    .filled::after {
      background-image: url(../img/star-filled.svg);
    }
  }
}

.tour__reviews {
  font-size: 12px;
  line-height: 16px;
  text-decoration: underline;
}