.banner {
  min-height: 323px;
  background-position: center;
  background-size: cover;

  h1 {
    span {
      color: $color-red;
    }
  }
}

.banner__body {
  display: flex;
  padding: 40px 0 50px;
  align-items: flex-start;
  flex-direction: column;

  @media (max-width: $tablets-max) {
    flex-direction: column;
  }

  @media (max-width: $mobiles-max) {
    padding: 25px 0;
  }
}

.banner__text {
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 24px;

  color: #131313;
  margin: 50px 0;

  @media (max-width: $mobiles-max) {
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 21px;
    margin: 40px 0 0 0;

    color: #131313;
  }
}

.banner__content {
  width: 70%;

  @media (max-width: $tablets-max) {
    width: 100%;
    margin-right: 0;
  }
}
