.filters {
  display: flex;
  flex-direction: column;
  @media (max-width: $mobiles-max) {
    width: 100%;
  }
}

.filters__group {
  margin-top: 25px;

  &:first-child {
    margin-top: 0;
  }
}

.filters__group-label {
  margin-bottom: 15px;
  display: block;
  font-weight: 500;
  line-height: 16px;
}

.filters__group-fieldset {
  display: flex;
  flex-direction: column;
  border: none;
  padding: 0;
  margin: 0;

  a {
    &:hover {
      color: $color-bright-red;
    }
  }
}

.filters__label {
  margin-bottom: 10px;
  line-height: 16px;
  display: block;

  input[type=radio] {
    display: inline-block;
    width: 20px;
    margin: 0;
    bottom: -0.5px;
    position: relative;

    &:before {
      content: "";
      height: 14px;
      width: 14px;
      display: block;
      left: 2px;
      position: relative;
      bottom: 1px;
      background-color: $color-light-gray;
      border: 1px solid $color-border-gray;
      border-radius: 100%;
    }

    &:checked:before {
      background-image: url(../img/radio-radio.svg);
      background-position: center center;
      background-repeat: no-repeat;
      background-size: auto;
    }
  }

  &.include {
    padding-left: 22px;
    background-image: url(../img/check-include.svg);
    background-repeat: no-repeat;
    background-size: 12px;
    background-position: left center;
  }

  input[type=checkbox] {
    display: inline-block;
    width: 20px;
    margin: 0;
    bottom: -1px;
    position: relative;

    &:before {
      content: "";
      height: 14px;
      width: 14px;
      display: block;
      left: 2px;
      position: relative;
      bottom: 1px;
      background-color: $color-light-gray;
      border: 1px solid $color-border-gray;
      border-radius: 3px;
    }

    &:checked:before {
      background-image: url(../img/check-check.svg);
      background-position: center center;
      background-repeat: no-repeat;
      background-size: auto;
    }
  }
}

fieldset[disabled] input[type=checkbox]+span,
fieldset[disabled] input[type=checkbox]+span+span,
fieldset[disabled] input[type=radio]+span,
fieldset[disabled] input[type=radio]+span+span,
input[type=checkbox].disabled+span,
input[type=checkbox].disabled+span+span,
input[type=checkbox][disabled]+span,
input[type=checkbox][disabled]+span+span,
input[type=radio].disabled+span,
input[type=radio].disabled+span+span,
input[type=radio][disabled]+span,
input[type=radio][disabled]+span+span {
  cursor: not-allowed !important;
  color: grey !important;
}

.filters__tours {
  float: right;
}
