.modal {
  position: relative;
  z-index: 999;
}
.modal__overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(45, 45, 45, 80%);
  z-index: 3;
  display: flex;
  justify-content: center;
  align-items: center;

  @media (max-width: $mobiles-max) {
    max-width: 100vw;
    max-height: 100vh;
  }
}

.modal__header {
  @media (max-width: $mobiles-max) {
    position: fixed;
    top: 0;
    width: 100%;
    padding: 30px 20px;
    min-height: 55px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .modal__title {
      margin: 0;
    }
    .modal__close {
      position: static;
    }
  }
}

.modal__body {
  background: $color-light-gray;
  overflow-x: hidden;
  width: calc(100% - 40px);
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 820px;

  @media (max-width: $mobiles-max) {
    width: 100vw;
  }

  &--callback {
    width: fit-content;
  }
}

.modal__close {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;

  .icon {
    stroke: $color-black;
    opacity: 0.5;
    transition: $transition-settings;
    cursor: pointer;

    &:hover {
      opacity: 1;
    }
  }

}

.modal__wrapper {
  background: $color-white;
  width: 100%;
  height: 100%;
  max-width: $container;
  margin: auto;
}

.modal__content {
  display: flex;
  justify-content: center;
  align-items: stretch;
  width: 100%;
  position: relative;
  overflow: hidden;

  .callback {
    max-width: 100%;
    margin-bottom: 0;
  }
}

.message {
  display: none;

  &--shown {
    position: fixed;
    top: 0;
    left: 0;
    background-color: $color-red;
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 500;
    z-index: 1000;
  }
}

.message__text {
  text-align: center;
  margin: 0 auto;
  color: $color-default-white
}

.success__title {
  margin-bottom: 30px;
}

.success__tlink {
  text-decoration: underline;
}

