.article-head__inner {
  min-height: 400px;
  position: relative;
  padding: 104px 0;
  background-repeat: no-repeat;
  background-size: cover;

  @media (max-width: $mobiles-max) {
    min-height: 315px;
    padding-top: 30px;
    padding-bottom: 30px;

    margin-left: -15px;
    margin-right: -15px;
    padding-left: 15px;
    padding-right: 15px;
  }

  &:after {
    content: "";
    background: rgba(0, 0, 0, 0.5);
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
  }

  h1 {
    text-transform: uppercase;
    font-size: 52px;
    line-height: 52px;
    font-family: "tt_slabsbold";
    color: #fff;
    text-align: center;
    position: relative;
    z-index: 2;
    margin: 54px 0;
    -moz-hyphens: auto;
    hyphens: auto;
    -ms-hyphens: auto;

    @media (max-width: $mobiles-max) {
      font-size: 40px;
    }

    @media (max-width: 500px) {
      font-size: 34px;
    }
  }
}

.article-head__contacts {
  position: relative;
  z-index: 2;
  margin: 0 auto;

  width: fit-content;

  &:after {
    position: absolute;
    content: "";
    background: #fff;

    bottom: -20px;
    height: 1px;
    width: 270px;

    left: 50%;
    transform: translate(-50%, 0%);
  }
  a {
    position: relative;
    display: inline-block;
    font-size: 18px;
    line-height: 18px;
    padding: 2px;
    transition: $transition-settings;
    text-transform: uppercase;

    margin: 0 10px;
    font-family: "Museo Sans", sans-serif;

    text-decoration: none;
    color: #fff;

    &:hover {
      color: #fff;
      background-color: #e0001c;
      text-decoration: none;
      border-radius: 3px;
    }
  }
}
