.infoblock {
  margin-top: 50px;
  padding: 30px 20px;
  display: flex;
  flex-direction: column;
  border-radius: 20px;
  background-color: $color-light-gray;
  @media (max-width: $mobiles-max) {
    margin-bottom: 30px;
  }

  hr {
    margin: 0 0 20px;
    border-top: 1px solid $color-gray;
  }
}

.infoblock__head {
  padding-bottom: 20px;
  justify-content: flex-start !important;
}

.infoblock__item {
  padding-bottom: 20px;
  color: $color-alt-black;

  &:last-child {
    padding-bottom: 0;
  }

  &--link {
    color: $color-light-black;
    font-weight: 500;

    a {
      transition: $transition-settings;
      font-size: 18px;

      &:hover {
        color: $font-color-red;
      }
    }

    span {
      transition: $transition-settings;
      cursor: pointer;
    }

    &:hover {
      color: $font-color-red;
    }

  }

  &-title {
    cursor: pointer;

    svg {
      width: 10px;
      transition: $transition-settings;
    }
    &--active {
       svg {
         transform: rotate(180deg);
       }
    }
  }


  &-text {
    color: $font-color-gray;
    margin-top: 20px;
    line-height: 15px;
    display: inline-block;

    a {
      color: $font-color-light-blue;
      &:hover {
         color: $font-color-red;
      }
    }
  }
}
