.reviews {
  margin-top: 25px;
  padding: 50px 0;
  overflow: hidden;
  position: relative;

  h2 {
    margin: 0 30px 0 0;

    @media (max-width: $mobiles-max) {
      margin: 0;
    }
  }

  .swiper-container {
    overflow: visible !important;
  }

  .swiper-button-prev.swiper-button-disabled,
  .swiper-button-next.swiper-button-disabled {
    opacity: 1 !important;
  }

  .swiper-reviews-prev,
  .swiper-reviews-next {
    background: rgba(0, 0, 0, 0.2);
    width: 160px;
    height: 160px;

    @media (max-width: $mobiles-max) {
      width: 80px;
      height: 80px;
      top: 245px !important;
    }

    &:after {
      display: none;
    }

    svg {
      fill: $color-default-white;
      width: 25px;
      height: 50px;

      @media (max-width: $mobiles-max) {
        width: 20px;
        height: 35px;
      }
    }

    &:hover {
      background: #fff;

      svg {
        fill: $color-red;
      }
    }
  }

  .swiper-reviews-prev {
    position: absolute;
    left: -80px;
    border: none;
    border-radius: 50%;
    padding: 0;

    svg {
      transform: rotate(180deg);
      position: absolute;
      left: 40px;

      @media (max-width: $mobiles-max) {
        left: 15px;
      }
    }

    @media (max-width: $mobiles-max) {
      left: -35px;
    }
  }

  .swiper-reviews-next {
    position: absolute;
    right: -80px;
    border: none;
    border-radius: 50%;
    padding: 0;

    svg {
      transform: rotate(-180deg);
      position: absolute;
      left: 40px;

      @media (max-width: $mobiles-max) {
        left: 15px;
      }
    }

    @media (max-width: $mobiles-max) {
      right: -35px;
    }
  }

  @media (max-width: $mobiles-max) {
    padding: 46px 0 50px;
  }
}

.reviews__swiper {
  margin-top: 0;
}

.reviews__content {
  font-size: 13px;
  font-weight: 400;
  line-height: 1.42857143;
}

.reviews__slide {
  box-shadow: 0px 0px 40px rgb(1 56 120 / 14%);
  padding: 30px 30px 15px 30px;
  width: 51% !important;
  min-height: 260px;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: auto !important;

  @media (max-width: $mobiles-max) {
    width: 100% !important;
    padding: 20px 15px 22px 15px;
    // min-height: 271px;
    min-height: 100%;
    justify-content: space-between;
  }
}

.reviews__date {
  line-height: 14px;
  font-size: 11px;
  color: #555;
}

.reviews__author {
  line-height: 20px;
  font-size: 16px;
  font-weight: 500;
  margin: 0;
}

.reviews__author-pic {
  width: 50px;
  height: 50px;
  margin-right: 15px;

  img {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
}

.reviews__buttons {
  position: absolute;
  top: -56px;
  right: 0;
  width: 100px;
  height: 50px;
  display: flex;
  @media (max-width: $mobiles-max) {
    position: relative;
    top: auto;
    display: flex;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 60px;
    justify-content: space-between;
    height: 30px;
    bottom: -15px;
  }
}

.reviews__container {
  position: relative;
}

.reviews__details {
  display: flex;

  @media (max-width: $mobiles-max) {
    flex-wrap: wrap;
  }
}

.reviews__author-info {
  display: flex;
  justify-content: space-between;

  @media (max-width: $mobiles-max) {
    width: 100%;
    justify-content: flex-start;
    margin-bottom: 14px;
  }
}

.reviews__link {
  text-decoration: underline;
  display: block;
  line-height: 13px;
  font-size: 13px;
  margin-left: auto;
  color: #000;
  margin-bottom: 15px;
  transition: $transition-settings;

  @media (max-width: $mobiles-max) {
    width: 100%;
  }
}

.reviews__titles {
  display: flex;
  align-items: center;
  margin: 0 0 60px 0;

  @media (max-width: $mobiles-max) {
    margin: 0 0 56px 0;
  }
}

.reviews__more {
  position: relative;
  margin-left: auto;
  padding-right: 20px;

  @media (max-width: $mobiles-max) {
    margin-top: 6px;
  }
  a {
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 16px;
    display: flex;
    align-items: center;
    text-align: right;

    color: #000000;
  }

  &:after {
    position: absolute;
    content: "";
    background-image: url("/dist/img/arrow-red.svg");
    height: 18px;
    width: 9px;
    right: 0;
    top: 0;
    background-repeat: no-repeat;
  }
}

.reviews__info {
  padding-top: 20px;
  border-top: 1px solid #eaeaea;
}
