.join {
  margin-bottom: 50px;
  overflow: hidden;

  h2 {
    margin: 50px 0;

    // @media (max-width: $mobiles-max) {
    //   margin: 0 0 50px 0;
    // }
  }
}

.join__widgets {
  display: flex;
  justify-content: space-around;
  align-items: unset;

  @media (max-width: $tablets-max) {
    flex-wrap: wrap;
    justify-content: space-around;
  }

  @media (max-width: $mobiles-max) {
    flex-direction: column;
    align-items: center;
  }
}

.join__widget {
  @media (max-width: $tablets-max) {

  }
  @media (max-width: 500px) {
    height: 150px;
    width: 300px;
    overflow: hidden;
    border-left: 1px solid rgb(4 47 89 / 12%);
    border-right: 1px solid rgb(4 47 89 / 12%);
    border-bottom: 1px solid rgb(4 47 89 / 12%);
  }
  #u_0_1_Rb {
    height: 141px !important;
  }
}
