.search {
  position: absolute;
  padding: 13px;
  width: 50px;
  display: flex;
  justify-content: flex-end;
  right: 90px;
  transition: all $transition-settings;
  cursor: pointer;

  &.active {
    width: 300px;
    background: inherit;
  }

  @media (max-width: $mobiles-max) {
    position: relative;
    right: 0;
    width: 100%;
    padding: 35px 0 15px;
  }
}

.search__input {
  width: 0;
  padding: 0;
  border: 0;
  @media (max-width: $mobiles-max) {
    width: 100%;
    padding: 10px 15px;

    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 16px;
    color: #828282;
  }
  &.active {
    width: 100%;
    background: inherit;
    outline: none;
    opacity: 1 !important;
  }
}
