.category {
  @media (max-width: $mobiles-max) {
    &--header {
      height: 100vh;
      background: #f7f6f6;
      margin-left: -24px;
      margin-right: -24px;
      padding-left: 24px;
      padding-right: 24px;
      padding-top: 30px;
      position: relative;

      .category__links {
        border: none;
        padding: 0;
        margin-top: 22px;
      }

      .category__links-item {
        font-size: 13px;
        line-height: 16px;
        margin-bottom: 12px;
      }

      .category__links-title {
        margin: 0;
        width: fit-content;
        position: relative;
        width: 100%;

        &:hover .category__dropdown-links {
          display: block;
        }

        a {
          color: #ec1c20;
          text-decoration: underline;
          display: inline;
        }
      }

      .category__title {
        margin-top: 0;
        margin-bottom: 10px;
        font-weight: 500;
        font-size: 13px;
        text-align: left;
      }
    }
  }
}

.category__dropdown-links {
  @media (max-width: $mobiles-max) {
    display: none;
    width: 100%;
    position: absolute;
    background: #ffffff;
    box-shadow: 0px 14px 16px rgb(0 0 0 / 8%);
    border-radius: 5px;
    z-index: 3;
    list-style: none;
    top: 22px;
    padding: 20px 20px 0 20px;

    .category__dropdown-link a {
      font-style: normal;
      font-weight: normal;
      font-size: 15px;
      line-height: 18px;
      color: #131313;
      text-transform: none !important;
      text-decoration: none;
      margin: 0 0 15px 0;
      padding: 0 0 14px 0 !important;
      border-bottom: 1px solid rgba(130, 130, 130, 0.2);
      display: block;

      &:hover {
        color: #ec1c20;
      }
    }

    .category__dropdown-link:last-of-type a {
      border-bottom: none;
    }

    .category__dropdown-link a::before {
      display: none !important;
    }
  }
}

.category__inner {
  display: flex;
  @media (max-width: $mobiles-max) {
    flex-direction: column;
  }
}

.category__sidebar {
  width: 100%;
  max-width: 26%;
  min-height: 1px;
  flex-shrink: 0;
  margin-right: 30px;
  min-width: 290px;
  @media (max-width: $mobiles-max) {
    max-width: 100%;
    margin-right: 0;
  }
}

.category__filters {
  padding: 30px 20px;
  border-radius: 20px;
  background: $color-light-gray;
}

.category__chosen {
  margin-bottom: 30px;

  &-title {
    margin: 20px 0;
    font-weight: 400;
  }

  &-items {
    display: flex;
    flex-wrap: wrap;
  }

  &-item {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin-right: 15px;
    padding: 6px 12px;
    border-radius: 4px;
    border: transparent;
    transition: $transition-settings;

    span {
      font-size: 16px;
    }

    svg {
      margin-left: 5px;
      stroke: $font-base-color;
      fill: $font-base-color;
    }

    &:hover {
      span,
      svg {
        color: $font-color-red;
        stroke: $font-color-red;
        fill: $font-color-red;
      }
    }
  }
}

.category__links {
  margin-top: 50px;
  padding: 30px 20px;
  border: 1px solid $color-border-gray;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  &-item {
    margin-bottom: 7px;
    transition: $transition-settings;
    &:hover,
    &--active {
      color: $color-bright-red;
    }
    &:last-child {
      margin: 0;
    }
  }
}

.category__tours {
  width: 100%;
}

.category__button {
  margin: 50px auto 30px;
  display: flex;
  justify-content: center;
}

.category__sort {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 35px;
  @media (max-width: $mobiles-max) {
    justify-content: flex-start;
    flex-wrap: wrap;
  }

  &-title {
    font-weight: 500;
    font-size: 18px;
    line-height: 28px;
    @media (max-width: $mobiles-max) {
      font-size: 14px;
      width: 100%;
    }
  }

  &-item {
    margin-left: 25px;
    position: relative;
    font-size: 16px;
    line-height: 28px;
    transition: 0.3s all;
    cursor: pointer;
    padding-right: 12px;
    @media (max-width: $mobiles-max) {
      font-size: 14px;
      margin: 0 25px 0 0;
    }

    &:hover {
      color: $font-color-red;
    }

    &--active {
      color: $font-color-red;
      text-decoration: underline;

      &::after {
        position: absolute;
        right: 0;
        content: url(../img/left-arrow.svg);
        transform: rotate(180deg);
      }
    }

    &--asc {
      &::after {
        transform: rotate(0deg);
      }
    }
  }
}
