.messengers__items {
  display: flex;
  align-items: center;
  margin: 50px 0 90px;
  flex-wrap: wrap;

  @media (max-width: $tablets-max) {
    margin: 50px 0;
  }

  @media (max-width: $mobiles-max) {
    flex-direction: column;
  }
}

.messengers__item {
  width: 23.5%;
  margin-right: 2%;

  @media (min-width: $desktops) {
    &:last-of-type {
      margin-right: 0;
    }
  }

  @media (max-width: $tablets-max) {
    width: 49%;
    margin-right: 2%;
    margin-bottom: 20px;

    &:nth-of-type(even) {
      margin-right: 0;
    }
  }

  @media (max-width: 600px) {
    width: 100%;
    margin-right: 0;
  }
}

.messengers__link {
  display: flex;
  align-items: center;

  font-family: "Open Sans", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 28px;
  text-decoration-line: underline;
  transition: $transition-settings;
  color: #2d2d2d;

  @media (max-width: $tablets-max) {
    justify-content: center;
  }

  @media (max-width: 600px) {
    justify-content: flex-start;
  }

  svg {
    margin-right: 17px;
  }

  &:hover {
    text-decoration: none;
  }
}

.messengers__text {
  margin: 0;
}
