// Default

$color-default-black: #000;
$color-default-white: #fff;
$color-transparent: rgba(0, 0, 0, 0);

/* general */
$mobiles: 320px;
$mobiles-max: 767px;
$tablets: 768px;
$tablets-max: 1023px;
$desktops: 1024px;
$container: 1290px;
$retina-dpi: 144dpi;
$retina-dppx: 1.5dppx;
$transition-settings: 0.3s all;

// Base

$color-black: #111;
$color-light-black: #222;
$color-alt-black: #333;
$color-middle-gray: #72717b;
$color-form-gray: #ededed;
$color-input-gray: #4f4f4f;
$color-border-gray: #828282;
$color-border-light-gray: #8c8c8c;
$color-btn-gray: #d4d4d4;
$color-gray: #c1c1c1;
$color-gray-line: #e8e7e7;
$color-light-gray: #f7f6f6;
$color-white: #fff;
$color-gold: #eecc7d;
$color-light-brown: #f7e5be;
$color-red: #cd1e19;
$color-dark-red: #9d1915;
$color-hover-red: #D83C38;
$color-active-red: #B7312D;
$color-bright-red: #f10000;

$font-light-gray: #cbcccc;
$font-color-gray: #777777;
$font-base-color: $color-black;
$font-color-blue: #23527c;
$font-color-light-blue: #337ab7;
$font-color-red: #ec1c20;
$background-base-color: $color-default-white;
$background-alt-color: #fafafa;
$color-placeholder: #d8d8d8;
