.popular-tours {
  overflow: hidden;
  margin-top: 25px;
}

.popular-tours__inner {
  h2 {
    margin: 0 30px 0 0;

    @media (max-width: $mobiles-max) {
      margin: 0 0 50px 0;
    }
  }
}

.popular-tours__swiper {
  margin-top: 48px;
  overflow: visible !important;

  @media (max-width: $mobiles-max) {
    margin-top: 25px;
  }
}

.popular-tours__item-info {
  margin-left: 20px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  z-index: 0;
  padding: 35px;

  @media (max-width: $mobiles-max) {
    padding: 35px 40px;
  }
}

.popular-tours__item {
  min-height: 400px;
  transition: $transition-settings;
  display: flex;
  position: relative;

  margin-bottom: 30px;

  img {
    position: absolute;
    object-fit: cover;
    width: 100%;
    height: 100%;
    z-index: -1;
  }

  &:first-of-type {
    width: 65.7%;
    margin-right: 2.3%;

    .popular-tours__item-info {
      margin-left: auto;
      width: 60%;
      align-items: flex-end;

      text-align: right;
      background: linear-gradient(
        270.53deg,
        #000000 0.34%,
        rgba(0, 0, 0, 0) 93.43%
      );
    }
  }

  &:nth-of-type(2n) {
    width: 32%;

    .popular-tours__item-info {
      margin-left: auto;

      background: linear-gradient(
        359.26deg,
        #000000 0.67%,
        rgba(0, 0, 0, 0) 69.17%
      );
    }
  }

  &:nth-of-type(3n) {
    width: 32%;
    margin-right: 2.3%;

    .popular-tours__item-info {
      margin-left: auto;

      background: linear-gradient(
        359.26deg,
        #000000 0.67%,
        rgba(0, 0, 0, 0) 69.17%
      );
    }
  }

  &:nth-of-type(4n) {
    width: 65.7%;

    .popular-tours__item-info {
      margin-left: auto;
      align-items: flex-end;

      text-align: right;
      width: 60%;
      background: linear-gradient(
        270.53deg,
        #000000 0.34%,
        rgba(0, 0, 0, 0) 93.43%
      );
    }
  }

  @media (max-width: $mobiles-max) {
    width: 100%;
    min-height: 360px;

    .popular-tours__item-info {
      margin: 0;
      width: 100%;
    }

    &:first-of-type {
      width: 100%;
      margin-right: 0;

      .popular-tours__item-info {
        width: 100% !important;
      }
    }

    &:nth-of-type(2n) {
      width: 100%;
    }

    &:nth-of-type(3n) {
      width: 100%;
      margin-right: 0;
    }

    &:nth-of-type(4n) {
      width: 100%;

      .popular-tours__item-info {
        width: 100% !important;
      }
    }
  }
}

.popular-tours__item-title {
  line-height: 34px;
  font-size: 30px;
  color: #fff;
  margin: 20px 0 40px;
  transition: $transition-settings;
  overflow: hidden;
  margin: 0;
  font-weight: 500;

  margin: 15px 0 40px;
}

.popular-tours__items {
  display: flex;
  flex-wrap: wrap;

  @media (max-width: $mobiles-max) {
    flex-direction: column;
  }
}

.popular-tours__titles {
  display: flex;
  align-items: center;

  @media (max-width: $mobiles-max) {
    flex-wrap: wrap;
  }
}

.popular-tours__swiper-wrapper {
  overflow: hidden;

  @media (max-width: $mobiles-max) {
    margin-left: -20px;
    margin-right: -20px;
  }
}

.popular-tours__item-dates {
  span {
    font-weight: 500;
    color: #fff;
    margin-left: 2px;
    font-size: 13px;
    transition: $transition-settings;
  }
}

.popular-tours__more {
  position: relative;
  margin-left: auto;
  padding-right: 20px;
  a {
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 16px;
    display: flex;
    align-items: center;
    text-align: right;

    color: #000000;
  }

  &:after {
    position: absolute;
    content: "";
    background-image: url("/dist/img/arrow-red.svg");
    height: 18px;
    width: 9px;
    right: 0;
    top: 0;
    background-repeat: no-repeat;
  }
}
