.contacts {
  h1 {
    font-weight: 600;
    font-size: 60px;
    color: #fff;
    text-transform: none;
    margin: 0;
    letter-spacing: 3px;
    text-align: center;
    padding: 125px 0;

    @media (max-width: $mobiles-max) {
      font-size: 40px;
    }
  }
}

.contacts__background {
  min-height: 323px;
  background-repeat: no-repeat;
  background-size: cover;

  @media (max-width: $mobiles-max) {
    min-height: 294px;
  }
}

.contacts__items {
  display: flex;
  flex-wrap: wrap;
  margin-top: 65px;

  @media (max-width: $mobiles-max) {
    margin-top: 50px;
  }
}

.contacts__item {
  font-family: "Open Sans", sans-serif;
  width: 32%;
  margin-right: 2%;
  box-shadow: 0 0 30px rgb(1 56 120 / 9%);

  border-radius: 3px;
  background: #fff;
  padding: 30px;
  margin-bottom: 15px;

  @media (min-width: $desktops) {
    &:nth-of-type(3n) {
      margin-right: 0;
    }
  }

  @media (max-width: $tablets-max) {
    width: 49%;
    margin-right: 2%;

    &:nth-of-type(2n) {
      margin-right: 0;
    }
  }

  @media (max-width: $mobiles-max) {
    width: 100%;
    margin-right: 0;
  }
}

.contacts__dept {
  font-weight: 600;
  font-size: 14px;
  line-height: 40px;
  text-align: left;
  margin-bottom: 10px;
}

.contacts__city {
  font-weight: 600;
  font-size: 14px;
  line-height: 40px;
  text-align: left;
  margin-bottom: 10px;

  color: #ffffff;

  padding: 0 20px;
  width: fit-content;
  border-radius: 3px;
  background-color: #ec1c20;
}

.contacts__phone {
  font-style: normal;
  font-weight: 600;
  font-size: 35px;
  line-height: 55px;

  color: #000000;

  @media (max-width: $mobiles-max) {
    font-size: 30px;
  }
}

.contacts__schedule {
  font-family: "Open Sans", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 36px;
  line-height: 56px;
  margin: 110px 0 50px;
  border-bottom: 1px solid #d8d8d8;

  color: #000000;
  padding-bottom: 40px;

  @media (max-width: $mobiles-max) {
    margin: 50px 0;
  }
}

.contacts__registry {
  color: red;
}

.contacts__about {
  color: #23527c;

  &:focus,
  &:hover {
    color: #f10;
    text-decoration: underline;
  }
}

.contacts__description {
  margin: 0;
}
