.breadcrumbs {
  flex-grow: 1;
  display: flex;
  padding: 30px 30px 0;

  h1 {
    font-size: 36px;
    color: $color-default-black;
    font-family: 'tt_slabsbold';
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    margin: 20px 0 30px;
  }

  a {
    color: $color-alt-black;
    font-size: 14px;
    text-decoration: none;
    font-weight: 400;
    line-height: 1.4;
    &:hover {
      color: $font-color-red;
    }
  }

  .tour__rating {
    margin: 0;
  }
}

.breadcrumbs__inner {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.breadcrumbs__list {
  display: flex;
  margin-bottom: 5px;

  @media (max-width: $mobiles-max) {
    display: none;
  }
}

.breadcrumbs__item {
  &:last-of-type {
    .breadcrumbs__divider {
      display: none;
    }
  }
}
