.reestr {
   display: flex;

  @media (max-width: $tablets-max) {
    margin: 20px 0;
    justify-content: center;
  }
 }

.reestr__logo {
   margin-right: 10px;
 }

.reestr__texts {
   display: flex;
   flex-direction: column;

   a {
     line-height: 15px;
     margin-bottom: 15px;
     color: $font-color-red;
   }

   p {
     margin: 0;
     line-height: 1.1;
     font-size: 12px;

     &.bold {
       font-size: 15px;
     }
   }
 }